
const _default = ()=>{
    return {
        id : -1,    
        isBoom : false,
        isBingo : false,
        isBgColor : '#fff'
    }
}
  
const _defaultArray = () => {
    let temp = [];
    temp.push(_default());
    temp.push(_default());
    temp.push(_default());
    temp.push(_default());
    temp.push(_default());
    return temp;
}
  
const _bingoData = () => {
    let idx = 0;
    let temp = [];
    temp.push(_defaultArray());
    temp.push(_defaultArray());
    temp.push(_defaultArray());
    temp.push(_defaultArray());
    temp.push(_defaultArray());

    temp.forEach(o=> o.forEach(a=> a.id = ++idx))

    return temp;
}


