<template>
  <div class="home">
    
    <div :class=" 'board_viewer board_view_mode_' + view_mode ">
      <div :class=" 'board_height_transition board_view_mode_' + view_mode + '_top'">

      </div>
      <div>
        <BingoBoard 
          :bingoDatas='bingoDatas' 
          :cell_click_handle='cell_click_handle'
          :size='size'/>

      </div>
      <div class="font-yello">현재 {{stack.length}}번 째 폭탄</div>
    </div>

    <div>
      {{msg}}
    </div>


    <div id="controller-container">
      <div id="contoller-box">
        
        <div class="font-white">
          <el-radio v-model="view_mode" label="nomal">정방형</el-radio>
          <el-radio v-model="view_mode" label="rotate">쿼터뷰</el-radio>
          <el-radio v-model="view_mode" label="3d">3D</el-radio>
        </div>
        
        <div class="font-white">
          <el-slider v-model="size" :step="10" :min="40" :max="100"></el-slider>
        </div>
        <el-divider></el-divider>

        <div class="font-white">
          <el-checkbox v-model="checked">위험지대 허용</el-checkbox>
          <el-checkbox v-model="checked">이난나 추적 알고리즘</el-checkbox>
        </div>
        <el-divider></el-divider>
        <div>
          <el-button size="small" @click="prev">이전으로</el-button>
          <el-button size="small" @click="reset">리셋</el-button>
          <el-button size="small" :type="'danger'" @click="stage_reset">스테이지 초기화</el-button>
          <!-- <el-button size="small" :type="'danger'" @click="test">test</el-button> -->
        </div>

      </div>
    </div>

  </div>
</template>

<script>
import BingoBoard from '@/components/BingoBoard.vue'
import bingoDatas from '../data/bingoData'
import bingo from '../service/bingo'

export default {
  name: 'Home',
  components: { BingoBoard },
  mounted() {
    this.bingoDatas = bingoDatas.default();
  },
  data() {
    return {
      bingoDatas : [],
      stack : [],
      stageSetupCount : 0 ,
      size : 70,
      checked:false,
      view_mode : '3d' // '3d' , 'nomal' , 'rotate'
    }
  },
  methods: {
    view_mode_changed(m){ this.view_mode = m; console.log(m)  },
    cell_click_handle(idx) {
      if(this.stageSetupCount == 2 ){
        let oldBingoBoard = JSON.parse(JSON.stringify(this.bingoDatas));
        this.stack.push(oldBingoBoard);
        bingo.boom(this.bingoDatas, idx);
      }
      else {
        bingo.cellChanged(this.bingoDatas, idx);
        this.stageSetupCount++;
      }
    },
    stage_reset(){
      this.stageSetupCount = 0;
      this.bingoDatas = bingoDatas.default();
      this.stack = [];
    },
    reset(){ while(this.stack.length > 0) this.prev() },
    prev(){ if(this.stack.length > 0) this.bingoDatas= [...this.stack.pop()];},
    test(){
      bingo.simulation(this.bingoDatas, 0)
    }
    
  },
}
</script>



<style scoped>
#controller-container{
    position: absolute;
    bottom: 50px;
    width: 100%;
}
#contoller-box{
  margin: 0 auto;
  position: relative;
  width: 300px;
}
</style>


<style>
.el-divider--horizontal{
    margin: 15px 0 !important;
}
</style>