const changd = (arr, row, col) =>{
    let cell = arr[row][col];
    if(!cell.isBingo){
        cell.isBoom = !cell.isBoom;
    }
}


const boom = (arr , id) =>{
    
    let row = _idByRow(id);
    let col = _idByCol(id);

    if( col[0] != 4) col.push(col[0]+1);    // 맨끝자리가 아닐시
    if( col[0] != 0) col.push(col[0]-1);    // 맨첫자리가 아닐시

    col.forEach(c=> changd(arr, row[0], c)) // 양옆 색칠

    if( row[0] != 4) row.push(row[0]+1);      // 맨아래가 아닐시
    if( row[0] != 0) row.push(row[0]-1);      // 맨위기 아닐시

    [...row].slice(1, 3).forEach(r=> changd(arr, r, col[0])) // 위아래 색칠

    
    col.forEach(c=> vertical(arr, c));
    row.forEach(r=> horizental(arr, r));
}

const horizental = (arr, row) =>{
    let isBingo = true;
    arr[row].forEach(t=> {
        if(!t.isBoom){
            isBingo = false;
            return false;
        }
    })
    
    arr[row].forEach(t=> {
        if(!t.isBingo){
            t.isBingo = isBingo
        }
    });
}

const vertical = (arr, col) =>{
    let isBingo = true;
    arr.forEach(t=> {
        if(!t[col].isBoom){
            isBingo = false;
            return false;
        }
    });
    arr.forEach(t=> {
        if(!t[col].isBingo){
            t[col].isBingo = isBingo
        }
    }); 
}

