// 연속 n 번 놓기

const print = (visited, arr) =>{
    

    let temp = [];
    for(let i = 0 ; i < visited.length; i ++){
        if(visited[i]){
            temp.push(i);
        }
    }
     
    let clone = JSON.parse(JSON.stringify(arr));
    let count = 0;

    temp.forEach((e,i) => {
        let id = e+1;
        // let col = _idByCol(id); 
        // let row = _idByRow(id);
        console.log(id , '터짐'); // 여기서 겹처깔기 허용할지 정해야함.
        boom(clone,id);
    });

    clone.forEach(a=> a.forEach(o=> {
        if(!o.isBoom){
            count ++;
        }
    }))


    // 여기서 count가 가장 많고 빙고가 되는것 찾아서 반환.
    console.log(clone);
    console.log(count);
    return temp;
}


const combination = (arr, visited, start, n, r) => {
    if(r == 0) {
        return print(visited , arr);
    }

    for(let i = start; i<n; i++) {
        visited[i] = true;
        combination(arr, visited, i + 1, n, r - 1);
        visited[i] = false;
    }
}


