<template>
    <span 
        :style="`height: ${this.size}px; width: ${this.size}px`"
        @click="()=>{ click_handle(id)}" 
            class="cell" 
            @mouseover="()=>{isHover = true}"
            @mouseout="()=>{isHover = false}">
        <div class="container">
            <div class="box" :style="bgStyle">
                <div class="boom" :class="boomStyle">
                </div>
            </div>
        </div>
    </span>
</template>

<script>
const props = {
    id : {
        type : Number,
        default : -1
    },
    isBingo : {
        type : Boolean,
        default : false
    },
    isBoom : {
        type : Boolean,
        default : false
    },
    click_handle : {
        type : Function,
        default : (i)=> {
            console.log(i)
        },
    },
    size : {
        type : Number,
        default : 50,
    },
}

export default {
    props,
    data() {
        return {
            isHover : false
        }
    },
    computed : {
        bgStyle : function(){ return this.id%2 == 1  ? "background : #801909" : "background : #131012"; },
        boomStyle : function(){
            if(this.isBoom){
                if(this.isBingo){ // 빙고
                    return "bingo_img";
                }else{ // 터짐
                    return "boom_img";
                }
            }
            return "";
        }
    },
}
</script>


<style scoped>
.cell{
    display: inline-block;
    border: 1px solid rgb(176, 72, 13);
    box-sizing: border-box;
}
.box{
    flex: 1 1 auto;
    height: 100%;
}
.container{
    position: relative;
    height: 100%;
    width: 100%;
}
.boom{
    /* opacity: 0.5; */
    height: 100%;
}
</style>